import {ADD_CONTENTFUL} from "./actions"



const initialState = {
    data:[],
    loading: false,
    hasErrors: false,
  }

function reducer(state=initialState, action){
    switch(action.type) {
        case ADD_CONTENTFUL:
            debugger;
          return Object.assign({}, state, 
              {
                data: [...state.data, action.data]
               }); 
         default: 
           return state;
     }
}


export default reducer