import React, {useState, useEffect} from 'react';
import ArticlesList from '../components/ArticlesList';
import PropTypes from 'prop-types'
import * as contentful from 'contentful'
import {addContentful} from '../modules/actions'
import { useDispatch } from 'react-redux';
import { connect } from 'react-redux';

const ArticlesListPage = ({setContentful=f=>f}) =>{ 
    const dispatch=useDispatch
    const [articleContent,setArticleContent]=useState();
    const getContentful = async()=>{
      debugger;
        let client= await contentful.createClient({
            space: process.env.REACT_APP_CONTENTFUL_SPACE_ID,
            accessToken: process.env.REACT_APP_CONTENTFUL_TOKEN
            })
            return client;
        }  
     useEffect(()=>{
          async function fetchData() {
            const cat= await getContentful();
            cat.getEntries().then(({items})=>{
                setArticleContent(items);
                setContentful(items);
            })
        }
        fetchData();
         },[]);



    return(
        <>
        <h1 className="fade-in">Articles</h1>
        {articleContent && <ArticlesList articles={articleContent} />}
        </>
)};

ArticlesListPage.propTypes = {
    setContentful: PropTypes.func.isRequired
  }

  const mapStateToProps = state => {
    return {
      content: state
    };
  };
  
  const mapDispatchToProps = dispatch => ({
      setContentful: data => dispatch(addContentful(data))
  });

  export default connect(undefined, mapDispatchToProps)(ArticlesListPage);
